import image from "../images/catalog-img3.png";
function Card(props) {
  return (
      <div class="card">
        <img class="card__image" src={props.card.image}/>
        <p class="card__title">JAYEONMAPPING ESSENCE MASK</p>
        <div class="card__bottom">
          <button class="card__link-btn" href={props.card.wbLink}>Посмостреть на сайте WB</button>
        </div>
      </div>
  );
}

export default Card;
