import React, { useDebugValue } from "react";
import {useFormWithValidation} from "./Validation";

function Form(props) {
  const [text, setText] = React.useState('Загрузить фото с отзывом');
  const form = React.useRef(null);
  const validation = useFormWithValidation();

  function handleSubmit(e) {
    e.preventDefault();
    const data = new FormData(form.current);
    props.form(data);
  }

  function handleChange(e) {
    validation.handleChangeFile(e);
    if (e.target.files.length > 0) {
      setText(e.target.files[0].name);
    } else {
      setText('Выберите фото c отзывом');
    }

  }
  return (
    /*     <section className="main">
            <form className="form-register" ref={form} onSubmit={Submit}>
            <h2 className="form-register__title">Регистрация в розыгрыше</h2>
            <input className="form-register__input" type="number" name="code" placeholder="Код сертификата" required/>
            <input className="form-register__input" type="text" name="surname" placeholder="Фамилия" required/>
            <input className="form-register__input" type="text" name="name" placeholder="Имя" required/>
            <input className="form-register__input" type="text" name="patronymic" placeholder="Отчество" required/>
            <input className="form-register__input" type="text" name="instagram" placeholder="Ваш инстраграм" required/>
            <input className="form-register__input" type="telephone" name="telephone" placeholder="Телефон для связи" required/>
            <input className="form-register__input form-register__input_type_file" type="file" name="feedbackWb" required/>
            <button type="submit" name="Sign Up">Зарегистироваться</button>
          </form>
            <div className="contacts">
              <a href="https://www.instagram.com/goldentree_ru/" target="_blank" className="contacts__link">
                <div className="contacts__instagram-image"></div>
                <p className="contacts__name">Instagram</p>
              </a>
              <a href="https://t.me/goldentreeru" className="contacts__link">
                <div className="contacts__telegram-image"></div>
                <p className="contacts__name">Telegram</p>
              </a>
              <a href="https://wa.me/message/7FFDUWLA7IHDA1" className="contacts__link">
                <div className="contacts__whatsapp-image"></div>
                <p className="contacts__name">WatsApp</p>
              </a>
            </div>
        </section> */

      <form className="form" ref={form} onSubmit={handleSubmit} noValidate>
        <h1 className="form__title">Регистрация в розыгрыше</h1>
        <input className="form__text" type="text" name="surname" placeholder="Фамилия*" pattern="[А-Яа-яЁё-]{1,}" alue={validation.values.surname} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.surname}</span>
        <input className="form__text" type="text" name="name" placeholder="Имя*" pattern="[А-Яа-яЁё-]{1,}" value={validation.values.name} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.name}</span>
        <input className="form__text" type="text" name="patronymic" placeholder="Отчество*" pattern="[А-Яа-яЁё-]{1,}" value={validation.values.patronymic} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.patronymic}</span>
        <input className="form__text" type="text" name="instagram" placeholder="Instagram*" pattern="@[a-zA-Z0-9._]{1,}" value={validation.values.instagram} onClick={validation.handleInstagram} onFocus={validation.handleClickInsta} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.instagram}</span>
        <input className="form__text" type="tel" name="telephone" placeholder="Телефон*" pattern="[0-9]{5,}" value={validation.values.telephone} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.telephone}</span>
        <input className="form__text" type="number" name="code" placeholder="Уникальный код*" value={validation.values.code} onChange={validation.handleChange} required/>
        <span className="form-error">{validation.errors.code}</span>
        <label className="form__label">
          <input className="form__input-file" name="feedbackWb" type="file" onChange={handleChange} required/>
          <span className="form__input-file-visible"></span>
          <span className="form__text-file">{text}</span>
        </label>
        <button className="form__button" type="submit" disabled={!validation.isValid}>Зарегистрироваться</button>
      </form>
  );
}

export default Form;
